import React, { useEffect, useState } from "react";
import Container from "@mui/material/Container";
import Box from "@mui/material/Box";
import Paper from "@mui/material/Paper";
import Pagination from "@mui/material/Pagination";
import Stack from "@mui/material/Stack";
import FormGroup from "@mui/material/FormGroup";
import FormControlLabel from "@mui/material/FormControlLabel";
import Radio from "@mui/material/Radio";
import RadioGroup from "@mui/material/RadioGroup";
import TextField from "@mui/material/TextField";
import Checkbox from "@mui/material/Checkbox";
import Button from "@mui/material/Button";
import FormControl from "@mui/material/FormControl";
import FormLabel from "@mui/material/FormLabel";
import FormHelperText from "@mui/material/FormHelperText";
import Alert from "@mui/material/Alert";
import AlertTitle from "@mui/material/AlertTitle";
import AssignmentTurnedInIcon from "@mui/icons-material/AssignmentTurnedIn";
import { data } from "./constant";
import ResponsiveAppBar from "./topBar";

const Main = () => {
    const [presentPageData, setPresentPageData] = useState([]);
    const [answers, setAnswers] = useState(
        [...Array(10)].map(() => {
            return {};
        })
    );
    const [errors, setErrors] = useState(
        [...Array(10)].map(() => {
            return false;
        })
    );
    const [currPage, setCurrPage] = useState(0);
    const [submitted, setSubmitted] = useState(false);

    const totalPages = Math.ceil(data.length / 10);

    const handleSubmit = (e) => {
        e.preventDefault();
        setSubmitted(true);
        presentPageData.map((val, ind) => {
            let errorPresent = false;
            val.Answer.split(",").map((ans, i) => {
                if (val.QUESTION_TYPE === "Single" && !answers[ind][ans]) {
                    errorPresent = true;
                    return;
                } else if (val.QUESTION_TYPE === "TF" && ans !== answers[ind]) {
                    errorPresent = true;
                    return;
                } else if (val.QUESTION_TYPE === "Fill") {
                    if (typeof answers[ind] === "object") {
                        errorPresent = true;
                        return;
                    } else if (
                        ans.toLowerCase() !== answers[ind].toLowerCase()
                    ) {
                        errorPresent = true;
                        return;
                    }
                }
            });
            setErrors((prev) => {
                let prepareErr = [...prev];
                prepareErr[ind] = errorPresent;
                return prepareErr;
            });
        });
    };

    const handleOptionChange = (questNo, e, type) => {
        if (type === "Single") {
            setAnswers((prev) => {
                let prepareAns = [...prev];
                prepareAns[questNo] = {
                    ...prepareAns[questNo],
                    ...(prepareAns[questNo][e.target.value] === undefined
                        ? { [e.target.value]: true }
                        : {
                              [e.target.value]:
                                  !prepareAns[questNo][e.target.value],
                          }),
                };
                return prepareAns;
            });
        } else if (type === "TF") {
            setAnswers((prev) => {
                let prepareAns = [...prev];
                prepareAns[questNo] = e.target.value;
                return prepareAns;
            });
        } else if (type === "Fill") {
            setAnswers((prev) => {
                let prepareAns = [...prev];
                prepareAns[questNo] = e.target.value;
                return prepareAns;
            });
        }
    };

    useEffect(() => {
        setSubmitted(false);
        setAnswers(
            [...Array(10)].map(() => {
                return {};
            })
        );
        setErrors(
            [...Array(10)].map(() => {
                return false;
            })
        );
        setPresentPageData(data.slice(currPage * 10, currPage * 10 + 10));
    }, [currPage]);
    return (
        <Container maxWidth="lg" sx={{ p: { xs: "0px" } }}>
            <ResponsiveAppBar />
            <Box sx={{ mb: "20px" }}>
                <form onSubmit={handleSubmit}>
                    {presentPageData.map((val, ind) => (
                        <Paper
                            key={ind}
                            elevation={3}
                            sx={{
                                pt: "5px",
                                pl: "20px",
                                pr: "20px",
                                pb: "20px",
                                mb: "20px",
                            }}
                        >
                            <FormControl
                                error={submitted && errors[ind]}
                                component="fieldset"
                                variant="standard"
                            >
                                <FormGroup>
                                    {val.QUESTION_TYPE === "Single" && (
                                        <div>
                                            <FormLabel id="demo-error-radios">
                                                Q{ind + 1}.{" "}
                                                {
                                                    val.QUESTION_TEXT.split(
                                                        /(\d+[.)])/
                                                    )[0]
                                                }
                                            </FormLabel>
                                            {val.QUESTION_TEXT.split(
                                                /(\d+[.)])/
                                            ).map((v, i) => (
                                                <div key={`${v}-${i}`}>
                                                    {i !== 0 && i % 2 === 0 && (
                                                        <FormControlLabel
                                                            control={
                                                                <Checkbox
                                                                    onChange={(
                                                                        e
                                                                    ) =>
                                                                        handleOptionChange(
                                                                            ind,
                                                                            e,
                                                                            val.QUESTION_TYPE
                                                                        )
                                                                    }
                                                                    value={
                                                                        i / 2
                                                                    }
                                                                />
                                                            }
                                                            label={v}
                                                        />
                                                    )}
                                                </div>
                                            ))}
                                        </div>
                                    )}
                                    {val.QUESTION_TYPE === "TF" && (
                                        <div
                                            key={`${val.QUESTION_TEXT}-${ind}`}
                                        >
                                            <FormLabel id="demo-error-radios">
                                                Q{ind + 1}. {val.QUESTION_TEXT}
                                            </FormLabel>
                                            <RadioGroup
                                                aria-labelledby="demo-radio-buttons-group-label"
                                                name="radio-buttons-group"
                                                onChange={(e) =>
                                                    handleOptionChange(
                                                        ind,
                                                        e,
                                                        val.QUESTION_TYPE
                                                    )
                                                }
                                            >
                                                <FormControlLabel
                                                    value="TRUE"
                                                    control={<Radio />}
                                                    label="True"
                                                />
                                                <FormControlLabel
                                                    value="FALSE"
                                                    control={<Radio />}
                                                    label="False"
                                                />
                                            </RadioGroup>
                                        </div>
                                    )}
                                    {val.QUESTION_TYPE === "Fill" && (
                                        <div key={`${val}-${ind}`}>
                                            <FormLabel id="demo-error-radios">
                                                Q{ind + 1}. {val.QUESTION_TEXT}
                                            </FormLabel>
                                            <br />
                                            <TextField
                                                label="Type here"
                                                error={submitted && errors[ind]}
                                                variant="standard"
                                                sx={{ my: "15px" }}
                                                onChange={(e) =>
                                                    handleOptionChange(
                                                        ind,
                                                        e,
                                                        val.QUESTION_TYPE
                                                    )
                                                }
                                            />
                                        </div>
                                    )}
                                </FormGroup>
                                {submitted && (
                                    <>
                                        {errors[ind] ? (
                                            <Alert severity="error">
                                                <AlertTitle>Oops!</AlertTitle>
                                                Correct Answers: {val.Answer}
                                            </Alert>
                                        ) : (
                                            <Alert severity="success">
                                                <AlertTitle>Hurray!</AlertTitle>
                                                You have selected correct
                                                answers
                                            </Alert>
                                        )}
                                        <FormHelperText>
                                            {val.explanation}
                                        </FormHelperText>
                                    </>
                                )}
                            </FormControl>
                        </Paper>
                    ))}
                    <Box
                        sx={{
                            display: "flex",
                            justifyContent: "space-between",
                        }}
                    >
                        <Stack mt={2} spacing={2}>
                            <Pagination
                                count={totalPages}
                                page={currPage + 1}
                                onChange={(e, v) => setCurrPage(v - 1)}
                                color="secondary"
                            />
                        </Stack>
                        <Button
                            variant="contained"
                            endIcon={<AssignmentTurnedInIcon />}
                            type="submit"
                        >
                            Verify
                        </Button>
                    </Box>
                </form>
            </Box>
        </Container>
    );
};

export default Main;
